<template>
	<div>
		<el-card class="content-card" shadow="hover">
			<div slot="header">
				<span>效果预览</span>
			</div>
			<div>
				<p><img src="../assets/UsbDisk/preview.png" width="250px"/></p>
			</div>
		</el-card>

		<el-card class="content-card" shadow="hover">
			<div slot="header">
				<span>方法：修改图标</span>
			</div>
			<div>
				<p>
					在优盘的根目录新建文件<q>autorun.inf</q>，文件内容如下&nbsp;&nbsp;&nbsp;
					[<a href="#" @click="copy('[autorun]\nICON=icon.ico,0')">复制</a>]
				</p>
				<p>
					<img src="../assets/UsbDisk/content.png" width="280px"/>
				</p>
				<p>
					选择喜欢的.ico图标文件，放到根目录下</p>
				<p>
					<img src="../assets/UsbDisk/file.png" width="200px"/>
				</p>
				<p>
					此时，优盘的图标就已经修改成功了，接下来要做的就是隐藏上图中的两个文件
				</p>
			</div>
		</el-card>

		<el-card class="content-card" shadow="hover">
			<div slot="header">
				<span>方法：隐藏文件</span>
			</div>
			<div>
				<p>
					快捷键 WIN+R 输入cmd，打开命令行
				</p>
				<p>
					<img src="../assets/UsbDisk/run.png" width="400px"/>
				</p>
				<p>
					切换到优盘所在位置（输入盘符加冒号，如E:），输入如下命令&nbsp;&nbsp;&nbsp;
					[<a href="#" @click="copy('attrib +a +s +r +h autorun.inf\nattrib +a +s +r +h icon.ico')">复制</a>]
				</p>
				<p>
					<img src="../assets/UsbDisk/cmd.png" width="280px"/>
				</p>
				<p>
					文件被隐藏，再次打开优盘就看不到文件了
				</p>
				<p>
					<img src="../assets/UsbDisk/hide.png" width="200px"/>
				</p>
			</div>
		</el-card>
		
		<el-card class="content-card" shadow="hover">
			<div slot="header">
				<span>方法：修改盘名</span>
			</div>
			<div>
				<p>
					鼠标右键点击优盘，选择属性，在输入框中输入新的盘名
				</p>
			</div>
		</el-card>
		
	</div>
</template>

<script>
	export default {
		name: 'UsbDisk',
		data() {
			return {}
		},
		methods: {
			copy(str) {
				let oInput = document.createElement('textarea');
				oInput.value = str;
				document.body.appendChild(oInput);
				oInput.select();
				document.execCommand("Copy");
				oInput.remove();
				this.$message.success({
					message: '复制成功'
				});
			}
		}
	}
</script>

<style>
</style>
